ks-listing-navigation {
    display: flex;
    justify-content: center;
    margin: 15px 0;
}

ks-listing-navigation[products="0"],
ks-listing-navigation[products="1"] {
    display: none;
}

ks-listing-navigation ks-filtering {
    order: 1;
    margin: 0 5px;
}

ks-listing-navigation ks-sorting {
    order: 3;
    margin: 0 5px;
}

ks-listing-navigation ks-pagination {
    order: 2;
    margin: 0 10px;
}

@media (max-width: 640px) {
    ks-listing-navigation {
        flex-wrap: wrap;
    }

    ks-listing-navigation ks-filtering {
        order: 1;
        margin: 0 3px;
        width: 40%;
        min-width: 144px;
        box-sizing: border-box;
    }
    
    ks-listing-navigation ks-sorting {
        order: 2;
        margin: 0 3px;
        width: 40%;
        min-width: 144px;
        box-sizing: border-box;
    }    

    ks-listing-navigation ks-pagination {
        order: 3;
        flex: 1;
        min-width: 100%;
        margin-top: 6px;
    }
}

@media (max-width: 344px) {
    ks-listing-navigation ks-filtering,
    ks-listing-navigation ks-sorting {
        width: 47%;
        min-width: 120px;
    }
}