ks-listing-header {
    display: block;
    position: relative;
    z-index: 1;
    padding: 15px;

    border-bottom: #ededed 1px solid;
    background-color: white;
    text-align: center;
    font-size: 0.875rem;
}

ks-listing-header .title {
    display: block;
    margin: 0 0 5px 0;
    
    font-family: var(--font-emphasis);
    font-weight: 700;
    font-size: 1.3rem;
    line-height: 1.3;
}

@media (max-width: 960px) {
    ks-listing-header .title {
        font-size: 1.105rem;
    }
}

ks-listing-header .autocorrect {
    font-size: 17px !important;
    font-weight: 500 !important;
    margin-bottom: 0px;
}

ks-listing-header .autocorrect > span {
    color: #ff3c3c;
    font-weight: 700;
}

ks-listing-header .description {
    max-width: 1200px;
    margin: 0 auto 15px auto;
    padding: 0 15px;

    line-height: 1.5;
}

ks-listing-header .tags {
    margin-top: 5px;
}

ks-listing-header hr {
    box-sizing: content-box;
    height: 0;
    overflow: visible;
    text-align: inherit;
    margin: 15px 0 15px 0;
    border: 0;
    border-top: 1px solid #e5e5e5;
}

/* THEMES */

ks-listing-header[theme="miele"] {
    background-color: #6b6b6b;
    color: white;
    border: none;
}

ks-listing-header[theme="miele"] ks-content-breadcrumbs * {
    color: white !important;
    background-color: #151515;
}

ks-listing-header[theme="miele"] ks-content-breadcrumbs>*:before {
    border-color: #151515;
    border-left-color: transparent;
}

ks-listing-header[theme="miele"] ks-content-breadcrumbs>*:after {
    border-left-color: #151515;
}

ks-listing-header[theme="miele"] hr {
    border-top: 1px solid #595959;
}



ks-listing-header[theme="smeg"] {
    background-color: #0e0e0e;
    color: white;
    border: none;
}

ks-listing-header[theme="smeg"] ks-content-breadcrumbs * {
    color: white !important;
    background-color: #272727;
}

ks-listing-header[theme="smeg"] ks-content-breadcrumbs>*:before {
    border-color: #272727;
    border-left-color: transparent;
}

ks-listing-header[theme="smeg"] ks-content-breadcrumbs>*:after {
    border-left-color: #272727;
}

ks-listing-header[theme="smeg"] hr {
    border-top: 1px solid #212121;
}

ks-listing-header[theme="smeg"] ks-content-tags>* {
    background: #272727;
}