import { Component, h, Prop } from '@stencil/core';

@Component({
    tag: 'ks-pagination',
    styleUrl: 'pagination.css'
})
export class Pagination {
    @Prop({reflect: true}) count: number;
    @Prop() current: number;
    @Prop() base: string = "";
    @Prop() pattern: string = "";

    render() {
        if(!this.count || !this.current || this.count < 2)
            return;

        let pages = new Array<number>();
        pages.push(this.current);
        let mobilePages = [...pages];

        let space = 6;
        let mobileSpace = 3;
        let left = this.current;
        let right = this.current;


        while (space > 0) {
            let leftFinished = false;
            let rightFinished = false;

            if (left-1 > 0) {
                pages.push(left-1);
                if(space >= mobileSpace) mobilePages.push(left-1);

                left--;
                space--;
            }
            else leftFinished = true;

            if (right+1 <= this.count) {
                pages.push(right+1);
                if(space >= mobileSpace) mobilePages.push(right+1);
                
                right++;
                space--;
            }
            else rightFinished = true;

            if(leftFinished && rightFinished)
                break;
        }

        pages.sort((a, b) => a - b);
        mobilePages.sort((a, b) => a - b);

        return[
            this.current > 1 ?
                <a href={this.link(this.current-1)} aria-label="Poprzednia strona">
                    <ks-icon name="chevron-left" size={0.9} aria-hidden/>
                </a>
            : null,

            pages.map(page => {
                if(page == this.current)
                    return <a class="active" aria-label={`Aktualna strona ${page}`} >{page.toString()}</a>

                const desktop = !mobilePages.includes(page) ? "desktop" : "";

                if(page == pages[0]){
                    const ret = <a class={desktop} href={this.link(1)} aria-label={`Strona ${1}`}>1</a>;
                    return page == 1 ? ret : [ret, <div class="separator"/>];
                }

                if(page == pages[pages.length - 1]){
                    const ret = <a class={desktop} href={this.link(this.count)} aria-label={`Strona ${this.count}`}>{this.count}</a>;
                    return page == this.count ? ret : [<div class="separator"/>, ret];

                }

                return <a class={desktop} href={this.link(page)} aria-label={`Strona ${page}`}>{page.toString()}</a>
            }),

            this.current < this.count ?
                <a href={this.link(this.current+1)} aria-label="NastÄ™pna strona">
                    <ks-icon name="chevron-right" size={0.9} />
                </a>
            : null
        ];
    }

    link(index: number): string {
        if(index == 1) return this.base;

        return this.base + this.pattern + index.toString();
    }
}
