import { Component, h, Element, Prop } from '@stencil/core';
import { NameLinkData } from "../../global/data/listing";

@Component({
    tag: 'ks-listing-header',
    styleUrl: 'listing-header.css'
})
export class ListingHeader {
    @Element() root: HTMLElement;
    @Prop() heading: string;
    @Prop() description: string;
    @Prop() query: string;
    @Prop() autocorrect: string;

    @Prop() breadcrumbs: NameLinkData[];
    @Prop() categories: NameLinkData[];
    @Prop() tags: NameLinkData[];

    @Prop({reflect: true}) theme: string;
    
    render() {
        const autocorrect = this.autocorrect?.split('{}');

        return[
            <h1 class="title">{this.heading}</h1>,
            (autocorrect?.length == 2 && this.query) ? 
                <h2 class="autocorrect">{autocorrect[0]}<span>{this.query}</span>{autocorrect[1]}</h2>
            : null,

            this.breadcrumbs?.length > 0 ?
                <ks-content-breadcrumbs items={this.breadcrumbs} />
            : null,

            this.description || this.categories?.length > 0 ? <hr/> : null,

            this.description ? <div class="description" innerHTML={this.description}/> : null,

            this.categories?.length > 0 ?
                <ks-content-tags items={this.categories} />
            : null,

            this.tags?.length > 0 ?
                <ks-content-tags class="tags" items={this.tags} />
            : null
        ];
    }
}